.App {
  font-family: sans-serif;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
}

* {
  box-sizing: border-box;
}

.transport-component {
  flex: 1 0 50%;
  border: 1px dotted gray;
  padding: 2px;
  word-break: break-all;
  min-width: 300px;
}

pre {
  white-space: break-spaces;
  word-break: break-word;
}
